  display: flex;
  font-size: 12px;
  align-items: center;
  color: var(--theia-descriptionForeground);
}

.theia-merge-editor .editor-pane > .header > .description .codicon {
  font-size: 14px;
  color: var(--theia-descriptionForeground);
}

